import React, { useState } from 'react';
import { Card, CardContent, Typography, Button, Box, Grid, Chip, TextField, IconButton, InputAdornment } from '@mui/material';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import PayoneerLogo from '../../assets/images/pay/payoneer.png';
import MercadoPagoLogo from '../../assets/images/pay/mp.png';
import PayPalLogo from '../../assets/images/pay/paypal.png';
import SkrillLogo from '../../assets/images/pay/skrill.png';
import PAYULogo from '../../assets/images/pay/payu.png';
import ePAYcoLogo from '../../assets/images/pay/epayco.png';

const WalletLinkingComponent = () => {
  const [showPassword, setShowPassword] = useState(false);

  const handleLinkPayoneer = () => {
    const clientId = 'TU_CLIENT_ID';
    const redirectUri = 'TU_REDIRECT_URI';
    const authorizationUrl = `https://login.payoneer.com/oauth/authorize?client_id=${clientId}&redirect_uri=${redirectUri}&response_type=code`;
    window.location.href = authorizationUrl;
  };

  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const renderComingSoonBadge = () => (
    <Chip
      label="Pr칩ximamente"
      color="secondary"
      size="small"
      sx={{
        position: 'absolute',
        top: 10,
        left: 10,
        backgroundColor: 'red',
        color: 'white',
        fontWeight: 'bold',
        zIndex: 1,
      }}
    />
  );

  return (
    <Box sx={{ padding: 3 }}>
      <Grid container spacing={4} justifyContent="center">

        {/* Tarjeta Payoneer (m치s peque침a) */}
        <Grid item xs={12} sm={6} md={4}>
          <Card sx={{ padding: 2, borderRadius: 3, boxShadow: 3, height: '100%', display: 'flex', flexDirection: 'column' }}>
            <CardContent sx={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
              <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                <img src={PayoneerLogo} alt="Payoneer" width="80%" />
              </Box>
              <Typography variant="h6" sx={{ textAlign: 'center', mb: 2 }}>
                Vincula tu Payoneer
              </Typography>
              <TextField label="Correo" variant="outlined" fullWidth sx={{ mb: 2 }} />
              <TextField
                label="Contrase침a"
                type={showPassword ? "text" : "password"}
                variant="outlined"
                fullWidth
                sx={{ mb: 2 }}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton onClick={togglePasswordVisibility} edge="end">
                        {showPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
              <Button
                onClick={handleLinkPayoneer}
                variant="contained"
                fullWidth
                sx={{
                  mt: 'auto',
                  borderRadius: 2,
                  padding: '10px',
                  fontWeight: 'bold',
                  fontSize: '1rem',
                  backgroundColor: '#97C703',
                  color: 'white',
                  '&:hover': { backgroundColor: '#7DAA02' },
                }}
              >
                Vincular Payoneer
              </Button>
            </CardContent>
          </Card>
        </Grid>

        {/* Fila de tarjetas desactivadas */}
        <Grid container spacing={4} justifyContent="center" sx={{ marginTop: 4 }}>
          
          {/* Tarjeta Mercado Pago */}
          <Grid item xs={12} sm={6} md={4}>
            <Card
              sx={{
                padding: 2,
                borderRadius: 3,
                boxShadow: 3,
                opacity: 0.5,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                cursor: 'not-allowed',
              }}
            >
              {renderComingSoonBadge()}
              <CardContent sx={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <img src={MercadoPagoLogo} alt="Mercado Pago" width="80%" />
                </Box>
                <Typography variant="h6" sx={{ textAlign: 'center', color: 'text.secondary' }}>
                  Mercado Pago
                </Typography>
                <Typography variant="body2" sx={{ textAlign: 'center', color: 'text.disabled' }}>
                  Esta opci칩n estar치 disponible en breve.
                </Typography>
              </CardContent>
            </Card>
          </Grid>


          <Grid item xs={12} sm={6} md={4}>
            <Card
              sx={{
                padding: 2,
                borderRadius: 3,
                boxShadow: 3,
                opacity: 0.5,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                cursor: 'not-allowed',
              }}
            >
              {renderComingSoonBadge()}
              <CardContent sx={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <img src={PayPalLogo} alt="paypal" width="80%" />
                </Box>
                <Typography variant="h6" sx={{ textAlign: 'center', color: 'text.secondary' }}>
                  Paypal
                </Typography>
                <Typography variant="body2" sx={{ textAlign: 'center', color: 'text.disabled' }}>
                  Esta opci칩n estar치 disponible en breve.
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          {/* Tarjeta Skrill */}
          <Grid item xs={12} sm={6} md={4}>
            <Card
              sx={{
                padding: 2,
                borderRadius: 3,
                boxShadow: 3,
                opacity: 0.5,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                cursor: 'not-allowed',
              }}
            >
              {renderComingSoonBadge()}
              <CardContent sx={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <img src={SkrillLogo} alt="Skrill" width="80%" />
                </Box>
                <Typography variant="h6" sx={{ textAlign: 'center', color: 'text.secondary' }}>
                  Skrill
                </Typography>
                <Typography variant="body2" sx={{ textAlign: 'center', color: 'text.disabled' }}>
                  Esta opci칩n estar치 disponible en breve.
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          {/* Tarjeta ePayco */}
          <Grid item xs={12} sm={6} md={4}>
            <Card
              sx={{
                padding: 2,
                borderRadius: 3,
                boxShadow: 3,
                opacity: 0.5,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                cursor: 'not-allowed',
              }}
            >
              {renderComingSoonBadge()}
              <CardContent sx={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <img src={ePAYcoLogo} alt="ePAYco" width="80%" />
                </Box>
                <Typography variant="h6" sx={{ textAlign: 'center', color: 'text.secondary' }}>
                  ePAYco
                </Typography>
                <Typography variant="body2" sx={{ textAlign: 'center', color: 'text.disabled' }}>
                  Esta opci칩n estar치 disponible en breve.
                </Typography>
              </CardContent>
            </Card>
          </Grid>


          <Grid item xs={12} sm={6} md={4}>
            <Card
              sx={{
                padding: 2,
                borderRadius: 3,
                boxShadow: 3,
                opacity: 0.5,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                cursor: 'not-allowed',
              }}
            >
              {renderComingSoonBadge()}
              <CardContent sx={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <img src={PAYULogo} alt="payulogo" width="80%" />
                </Box>
                <Typography variant="h6" sx={{ textAlign: 'center', color: 'text.secondary' }}>
                  PAYU
                </Typography>
                <Typography variant="body2" sx={{ textAlign: 'center', color: 'text.disabled' }}>
                  Esta opci칩n estar치 disponible en breve.
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          
        </Grid>

      </Grid>
    </Box>
  );
};

export default WalletLinkingComponent;
